@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,300;1,300&display=swap);
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #ddd;
}

#loading-box {
  width: 100vw;
  height: 100vh;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes slide-down{

  100%
  {
    opacity:1;
    transform:none;
  }
}

@keyframes slide-down{

  100%
  {
    opacity:1;
    transform:none;
  }
}

#login-box {
  padding: 25px;
  border-radius: 8px;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 30vw;
  box-shadow: 0 8px 6px -6px #bbb;
  transform:translateY(-4rem);
  opacity:0;
  -webkit-animation:slide-down 0.75s cubic-bezier(.08,.5,.66,1) forwards;
          animation:slide-down 0.75s cubic-bezier(.08,.5,.66,1) forwards;
}

#login-box #logo {
  height: 120px;
  width: 120px;
}

#login-box #login-button {
  border-radius: 8px;
  background-color: #658a99;
  padding: 8px;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  border:2px solid #658a99;
  align-items: center;
  margin-top: 25px;
  transition:all 0.2s ease-in-out;
}


#login-box #login-button:hover{
  background-color:white;
  border:2px solid #658a99;
  color:#658a99;
}

#login-box #details{
  text-align: center;
  font-size:1rem;
  color:#aaa;
  margin-top:25px;
  border-top:2px solid #ddd;
  padding-top:20px;
  line-height: 2rem;
}

#login-box #details a, a:visited{
  text-decoration:none;
  color:#4e7686;
}

#login-box #details a:hover{
  text-decoration:underline;
}


@media all and (max-width:1250px){
  #login-box {
    width:50vw;
  }
}


@media all and (max-width:850px){
  #login-box {
    width:70vw;
  }
}
