#body{
  width:85vw;
  margin:auto;
  padding-bottom:50px;
}


#body #widget-grid{
  display:grid;
  box-sizing: border-box;
  grid-template-columns:1fr minmax(500px,1fr);
  grid-gap:1rem;
}

#body #left-1,#body #right-1{
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  display:flex;
  flex-direction: column;
}


@media all and (max-width:1250px){
  #body{
    width:90vw;
  }
}

