@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,300;1,300&display=swap');

body {
  margin: 0;
  /* font-family: 'Inter', sans-serif; 
  font-family: 'Miriam Libre', sans-serif;*/
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight:bold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor:default;
  /* background-color: #a1c3d1; */
  /* background-image:linear-gradient(to top, rgba(23, 32, 42, 0.20), rgba(0, 0, 0, 0.80)), url("https://picsum.photos/1920/1080?random=6"); */
  background-size: cover;
  background-position:center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* .vanta-canvas {
  width:100% !important;
  position: fixed !important;
} */


#bg{
  /*height and width are important*/
  width:100%;
  height:100vh; 
}

#titleandlogo{
  margin:auto;
  margin-top:60px;
  margin-bottom:75px;
  display:flex;
  width:95vw;
  align-items: center;
}

#titleandlogo #title{
  color:white;
  font-size:1rem;
  margin-left:10px;
  letter-spacing: .5rem;
}

#titleandlogo #logo{
  height:40px;
}

#titleandlogo #time{
  font-size:0.8rem;
  margin:auto;
  margin-right:0;
  color:white;
  /* padding:2px 10px; */
}

#titleandlogo #options-box{
  display:flex;
  align-items:center;
  margin-left:15px;
}

#titleandlogo #options-box img{
  cursor:pointer;
  object-fit:cover;
  border-radius:50%;
  height:45px;
}

#holder{
  position:absolute;
  margin-top:-70px;
  width:95vw;
}


#holder #options-list{
  background-color:white;
  margin:auto;
  margin-right:0;
  width:fit-content;
  display:none;
  opacity:0;
  transform:translateY(-0.75rem);
  top:95px;
  border-radius:8px;
  overflow: hidden;
  box-shadow:2px 2px 10px #1a1a1a;
}

@keyframes slide-down{

  100%
  {
    opacity:1;
    transform:none;
  }
}

#holder #options-list div:nth-child(1){
  padding:15px;
}

#holder #options-list div:nth-child(1):hover{
  background-color:#eee;
  cursor:pointer;
  transition:all .25s ease-in-out;
}

#holder #options-list #logout-button{
  cursor:pointer;
  width:100%;
  padding:15px;
  box-sizing: border-box;
  background-color:#658a99;
  color:white;
  text-align: center;
}

#body{
  width:85vw;
  margin:auto;
  padding-bottom:50px;
}


#body #widget-grid{
  display:grid;
  box-sizing: border-box;
  grid-template-columns:1fr minmax(500px,1fr);
  grid-gap:1rem;
}

#body #left-1,#body #right-1{
  height:fit-content;
  display:flex;
  flex-direction: column;
}


@media all and (max-width:1250px){
  #body{
    width:90vw;
  }
}

@media all and (max-width:1000px){

  #body{
    width:75vw;
  }
  #body #widget-grid{
    grid-template-columns:1fr;
  }

  #titleandlogo #title{
    display:none;
  }

  #titleandlogo #time{
    font-size:0.7rem;
  }

  #titleandlogo #logo{
    height:35px;
  }

  #titleandlogo #options-box img{
    height:40px;
  }
}

@media all and (max-width:769px){

  #body{
    width:90vw;
  }

  #titleandlogo, #holder{
    width:90vw;
  }
}

@media all and (max-width:400px){

  #body{
    width:92vw;
  }
}